import {Injectable, NgZone, OnDestroy} from '@angular/core';
import {Subject} from 'rxjs';
import {AdaptRadarURLChangedEventData} from '../adapt-radar.model';

interface MsToTimeFormat {
  timeInSeconds: number;
  fullTime: string;
}

/**
 * Service used to track url changes
 */
@Injectable({providedIn: 'root'})
export class AdaptRadarURLChangeTracker implements OnDestroy {
  private _urlChange$: Subject<AdaptRadarURLChangedEventData>;
  private _previousLocation: Location = {...location};
  private _startTime: number = performance.now();
  private _endTime: number;

  private _urlChangeCallbackBind: () => void = this._urlChangeCallback.bind(this);

  constructor(private _ngZone: NgZone) {
  }

  urlChange(): Subject<AdaptRadarURLChangedEventData> {
    if (this._urlChange$) {
      return this._urlChange$;
    } else {
      this._urlChange$ = new Subject<AdaptRadarURLChangedEventData>();
    }

    this._ngZone.runOutsideAngular(() => {
      document.addEventListener('click', this._urlChangeCallbackBind, true);
    });

    return this._urlChange$;
  }

  unsubscribeFromURLChange(): void {
    document.removeEventListener('click', this._urlChangeCallbackBind);
    this._urlChange$ = null;
  }

  ngOnDestroy(): void {
    this.unsubscribeFromURLChange();
  }

  private _msToTime(duration: number): MsToTimeFormat {
    const milliseconds = Math.floor((duration % 1000) / 100);
    const seconds = Math.floor((duration / 1000) % 60);
    const minutes = Math.floor((duration / (1000 * 60)) % 60);
    const hours = Math.floor((duration / (1000 * 60 * 60)) % 24);

    return {
      timeInSeconds: seconds,
      fullTime: `${(hours < 10) ? '0' + hours : hours}:${(minutes < 10) ? '0' + minutes : minutes}:${(seconds < 10) ? '0' + seconds : seconds}.${milliseconds}`,
    };
  }

  private _urlChangeCallback(): void {
    if (this._previousLocation.href !== location.href) {
      this._endTime = performance.now();

      const locationCopy = {...location};
      const timeOnPage = this._endTime - this._startTime;
      const msToTime = this._msToTime(timeOnPage);

      this._urlChange$.next({
        PreviousLocation: {...this._previousLocation},
        CurrentLocation: locationCopy,
        PageTitle: document.title,
        TimeOnPageInMilliseconds: timeOnPage,
        TimeOnPageInSeconds: msToTime?.timeInSeconds,
        TimeOnPage: msToTime?.fullTime
      });

      this._previousLocation = locationCopy;
      this._startTime = performance.now();
    }
  }
}
