// Generic events that can be applied across all components
export enum AdaptRadarCustomEventType {
  ErrorFired = 'ErrorFired', // Error shown to user
  Init = 'Init', // Component loaded(OnInit)
  Focus = 'Focus', // User focus into target
  Blur = 'Blur', // User blur from target
  Click = 'Click', // User click on target. Same event type is fired when used press target from the keyboard
  Search = 'Search', // User run search
  ValueChanged = 'ValueChanged', // Data was changed
  Clear = 'Clear', // Target is cleared
  DragStart = 'DragStart', // User start to drag target
  Drop = 'Drop', // User drop target after dragging
}

// Component specific events
export enum AdaptRadarComponentEventType {
  TabAdd = 'TabAdd', // Fires when Tab was added
  TabDelete = 'TabDelete', // Fires when Tab was deleted
}

export interface AdaptRadarGlobalData {
  BrowserType: string; // Browser type or name (eg. Firefox, Chrome, Safari, etc.)
  BrowserVersion: string; // Major browser version number
  BrowserDetailedVersion: string; // Detailed browser version

  // MOBILES & TABLETS Device info
  DeviceType: string; // "mobile" or "tablet"
  DeviceVendor: string; // Vendor name (eg. Apple, Samsung, Xiaomi, etc.)
  DeviceModel: string; // Device model (eg. iPhone, iPad, Pixel, Redmi)

  OperatingSystem: string; // Operating system name (eg. Mac OS, Windows, etc.)
  OperatingSystemVersion: string; // Detailed operating system version

  // Experimental API. Not fully supported by all browsers
  // https://developer.mozilla.org/en-US/docs/Web/API/NetworkInformation
  ConnectionDownlink: number; // Returns the effective bandwidth estimate in megabits per second, rounded to the nearest multiple of 25 kilobits per seconds.
  ConnectionEffectiveType: string; // Returns the effective type of the connection meaning one of 'slow-2g', '2g', '3g', or '4g'. This value is determined using a combination of recently observed round-trip time and downlink values.

  // Experimental API. Not fully supported by all browsers
  // https://developer.mozilla.org/en-US/docs/Web/API/Navigator/languages
  LocalizationSettings: readonly string[]; // Returns an array of DOMStrings representing the user's preferred languages. The language is described using language tags according to RFC 5646: Tags for Identifying Languages (also known as BCP 47). In the returned array they are ordered by preference with the most preferred language first.

  CurrentLanguage: string; // Returns a string representing the preferred language of the user, usually the language of the browser UI.

  // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Intl/DateTimeFormat/resolvedOptions
  TimeZone: string; // Representing the runtime's default time zone
  PageName: string; // Returns current page title "document.title"

  AngularVersion: string; // Returns current Angular version. Used "import {VERSION} from '@angular/core'";

  ADAPTVersion: string; // Detailed "adapt-angular" library version
  TableVersion: string; // Detailed "adapt-table" library version
  CSSVersion: string; // Detailed "adapt-css" library version
  GanttVersion: string; // Detailed "adapt-gantt" library version
  ObsoleteVersion: string; // Detailed "adapt-obsolete" library version
  RTEVersion: string; // Detailed "adapt-rte" library version
  ChartsVersion: string; // Detailed "adapt-charts" library version
  RadarVersion: string; // Detailed "adapt-radar" library version

  AccessibilityMode: boolean; // Is accessibility css used
  DarkMode: boolean; // Is dark-mode css used
  Customized: boolean; // Is customized css used
}

export interface AdaptRadarURLChangedEventData {
  PreviousLocation: Location; // Represents the previous location (URL) of the object it is linked to
  CurrentLocation: Location; // Represents the current location (URL) of the object it is linked to
  PageTitle: string; // Represents current page title "document.title"
  TimeOnPage: string; // Time that user spent on page in format "hors:minutes:seconds.milliseconds / 00:00:00.00"
  TimeOnPageInSeconds: number; // Time that user spent on page in seconds
  TimeOnPageInMilliseconds: number; // Time that user spent on page in milliseconds
}

// TODO: Think can we merge AdaptRadarURLChangedCollectedEventData and AdaptRadarPagePathEventData and use only one
export interface AdaptRadarURLChangedCollectedEventData {
  PageTitle: string; // Represents current page title "document.title"
  TimeOnPage: string; // Time that user spent on page in format "hors:minutes:seconds.milliseconds / 00:00:00.00"
  TimeOnPageInSeconds: number; // Time that user spent on page in seconds
  TimeOnPageInMilliseconds: number; // Time that user spent on page in milliseconds
}

export interface AdaptRadarPagePathEventData {
  url: string;
}

// List of supported providers services
export type AdaptRadarSupportedProvidersServices =
  AdaptRadarGoogleGlobalSiteTagProvider
  | AdaptRadarGoogleTagManagerProvider
  | AdaptRadarGainsightProvider;

export type AdaptRadarCustomEventData<T = unknown> = T;

export interface AdaptRadarConfig {
  // Array of analytics providers from `AdaptRadarSupportedProviders` that should be initialized
  providers: AdaptRadarProviderConfig[];

  /**
   * Product information that will be automatically set into each global context request
   *
   * Example:
   * productInfo: {
   *   productArea: {
   *     name: AdaptRadarProductAreaName.Platform,
   *     version: new Version('11.22.33'),
   *     hosting: AdaptRadarHostingType.OnPrem
   *   },
   *   subProductLevel1: {
   *     name: AdaptRadarSubProductNameLevel1.Configure,
   *     version: new Version('22.33.44'),
   *     hosting: AdaptRadarHostingType.OnPrem
   *   },
   *   subProductLevel2: {
   *     name: AdaptRadarSubProductNameLevel2.InnovationSuiteConfigurations,
   *     version: new Version('33.44.55'),
   *     hosting: AdaptRadarHostingType.SaaS
   *   },
   *   subProductLevel3: {
   *     name: AdaptRadarSubProductNameLevel3.DataManagement,
   *     version: new Version('44.55.66'),
   *     hosting: AdaptRadarHostingType.SaaS
   *   }
   * }
   */
  productInfo?: AdaptRadarProductInfo;

  /**
   * The Map of component/directive names that should not send events
   *
   * Example:
   *
   * disableEventSending: new Map([
   *   [AdaptRadarCustomEventName.AdaptRxTextfield, true],
   *   [AdaptRadarCustomEventName.AdaptButton, true],
   * ])
   */
  disableEventSending?: AdaptRadarDisableEventSendingConfig;
}

export interface AdaptRadarProviderConfig {

  // Provider name from `AdaptRadarSupportedProviders`
  name: AdaptRadarSupportedProviders;

  // id of provider
  id: string;

  // Can be used to pass own source script for provider. If passed "false" that mean that source script wouldn't be injected at all, this can be useful if there is requirement to insert script manually on the page(eg. CSP)
  sourceScript?: string | false;

  // Can be used to pass own init script for provider.  If passed "false" that mean that source script wouldn't be injected at all, this can be useful if there is requirement to insert script manually on the page(eg. CSP)
  initScript?: string | false;

  // Can be used if provider need to be disabled. This is mean that it wouldn't be initialized
  disabled?: boolean;

  // The idea under this property is to allow customers provide array of predefined actions that will be automatically triggered in specific places.
  // As a good example for such action will be Gainsight identify event that need to be sent once for correct analytics
  oneTimeActions?: AdaptRadarGainsightIdentifyAction[];
}

/**
 * Use separate config for module to have possibility to set needed data even if customer didn't start collecting data
 *
 * For example, we may want always set ADAPT versions for all environments(dev, qa)
 * But customer may want to not enabling data collection for some environments
 * That is why we shouldn't set versions in scope of "AdaptRadarConfig"
 */
export interface AdaptRadarModuleConfig {
  /**
   * Configuration that used to set library versions that used in application
   * version for each library can be imported from library package the save way as angular VERSION
   *
   * Example:
   * import {ADAPT_ANGULAR_VERSION} from '@bmc-ux/adapt-angular';
   * import {ADAPT_TABLE_VERSION} from '@bmc-ux/adapt-table';
   */
  versions: AdaptRadarLibraryVersions;
}

export interface AdaptRadarOneTimeAction<T, U> {
  name: T;
  data: U;
}

// Interface represent hashmap with adapt libraries versions
export interface AdaptRadarLibraryVersions {
  ADAPT_ANGULAR_VERSION?: Version;
  ADAPT_AVATAR_VERSION?: Version;
  ADAPT_CHATBOT_VERSION?: Version;
  ADAPT_CHARTS_VERSION?: Version;
  ADAPT_CUSTOMIZATION_VERSION?: Version;
  ADAPT_GANTT_VERSION?: Version;
  ADAPT_OBSOLETE_VERSION?: Version;
  ADAPT_RTE_VERSION?: Version;
  ADAPT_TABLE_VERSION?: Version;
  ADAPT_RADAR_VERSION?: Version;
  ADAPT_CSS_VERSION?: Version;
}

export type AdaptRadarDisableEventSendingConfig = Map<AdaptRadarCustomEventName, boolean>;
