import {AdaptRadarComponentEventType, AdaptRadarCustomEventName, AdaptRadarCustomEventType} from './adapt-radar.model';


type FormControlSize = 'default' | 'sm' | 'lg';

type AdaptInteractionSource = 'keyboard' | 'mouse' | 'touch';

type AdaptRadarBooleanState = 'on' | 'off';

enum AdaptNumberType {
  Integer = 0,
  Float = 1,
  Scientific = 2
}

/* generic component data */

export interface AdaptRadarGenericComponentData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  ComponentName: AdaptRadarCustomEventName; // Name of the component that is fired event
  PageTitle: string; // Page title
  Date: Date; // Javascript Date object
  ISODate: string; // Date in ISO format
  Timestamp: number; // Current timestamp
}


interface AdaptRadarComponentRxCommonData {
  GroupName: string; // Used to combine Rx- controls into groups. The value takes from [adaptRadarGroupName] directive
}

interface AdaptRadarComponentRxGenericData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Form element label
  Required: boolean; // Is form element required
  Disabled: boolean; // Is form element disabled
  ReadOnly: boolean; // Is form element readonly
}


/* rx-textfield */

interface AdaptRadarComponentRxGenericTextData extends AdaptRadarComponentRxGenericData {
  Badge?: boolean; // Is custom tag provided
  BadgeText?: string; // Custom tag text
  Hinted: boolean; // Is tooltip provided // Is tooltip provided
  Subtext: boolean; // Is sub label text provided
  // DynamicEntry: boolean; // Is the value was entered by user or by system
  TextEntered: boolean; // Was data entered into form element or not
  CharCount: number; // Number of characters that was entered
  FieldSize: FormControlSize; // Size of the form element
}

export type AdaptRadarComponentRxTextfieldData = AdaptRadarComponentRxGenericTextData;

export type AdaptRadarComponentRxTextareaData = AdaptRadarComponentRxGenericTextData;


/* rx-counter */

export interface AdaptRadarComponentRxCounterData extends AdaptRadarComponentRxGenericData {
  NumberType: AdaptNumberType; // Type of the counter component
  TextEntered: boolean; // Was data entered into form element or not
  CharCount: number; // Number of characters that was entered
  Hinted: boolean; // Is tooltip provided
  Subtext: boolean; // Is sub label text provided
  // DynamicEntry: boolean; // Is the value was entered by user or by system
  FieldSize: FormControlSize; // Size of the form element
  Target?: 'increase' | 'decrease'; // What control element was triggered
}


/* rx-search */

interface AdaptRadarComponentRxSearchChangedData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  SearchMethod: 'filter' | 'query';
  FieldSize: FormControlSize; // Size of the form element
  TextEntered: boolean; // Was data entered into form element or not
  SearchButton: boolean; // Is search button enabled
  InputMethod: AdaptInteractionSource; // From what source interaction was provided
}

interface AdaptRadarComponentRxSearchClearData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Form element label
  Required: boolean; // Is form element required
  TextEntered: boolean; // Was data entered into form element or not
}

export type AdaptRadarComponentRxSearchData =
  (AdaptRadarComponentRxSearchChangedData | AdaptRadarComponentRxSearchClearData) & AdaptRadarComponentRxCommonData;


/* rx-checkbox */

export interface AdaptRadarComponentRxCheckboxData extends AdaptRadarComponentRxGenericData {
  /*CheckboxGroup: boolean;*/
  /*CheckboxLabel: string;*/
  DefaultState: AdaptRadarBooleanState; // What was state on the init stage
  CurrentState: AdaptRadarBooleanState; // Current state
}


/* rx-radiobutton */

export interface AdaptRadarComponentRxRadiobuttonData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  // DefaultState: AdaptRadarBooleanState; TODO: What is sense of this property
  CurrentState: AdaptRadarBooleanState; // State after changes. TODO: If we can't uncheck radio button this value always will be "on"
  Disabled: boolean; // Is form element disabled
  ReadOnly: boolean; // Is form element readonly
  Label: string; // Form element label
  RadioGroup: boolean; // Is radio buttons used inside button group
}


/* rx-rating */

enum RxRatingSize {
  xs = 0,
  sm = 1,
  md = 2,
  lg = 3
}

enum RxRatingVotingType {
  Star = 0,
  Flag = 1,
  Like = 2,
  Thanks = 3
}

export interface AdaptRadarComponentRxRatingDefaultData extends AdaptRadarComponentRxGenericData {
  FieldSize: RxRatingSize; // Size of the form element
  RatingEntered: boolean; // Was rating entered or not
  RatingMax: number; // Maximum allowed rating value
  RatingValue: number; // Current rating value
  SubmitMethod?: AdaptInteractionSource; // From what source submission was provided
}

export interface AdaptRadarComponentRxRatingBinaryData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Element label
  RatingEntered: boolean; // Was rating entered or not
  RatingValue: number; // Current rating value
  SubmitMethod: AdaptInteractionSource; // From what source submission was provided
}

export interface AdaptRadarComponentRxRatingLikeData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Element label
  FieldSize: RxRatingSize; // Size of the form element
  RatingEntered: boolean; // Was rating entered or not
  RatingValue: number; // Current rating value
  Disabled: boolean; // Is element disabled
  SubmitMethod: AdaptInteractionSource; // From what source submission was provided
}

export interface AdaptRadarComponentRxRatingVotingData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Type: RxRatingVotingType; // Type of the Voting rating component
  HasTooltip: boolean; // Is tooltip enabled
  HasThanksText: boolean; // Is "thanks" text provided
  RatingValue: boolean; // Current rating value
  SubmitMethod: AdaptInteractionSource; // From what source submission was provided
}

/* rx-switch */

export interface AdaptRadarComponentRxSwitchData extends AdaptRadarComponentRxGenericData {
  DefaultState: AdaptRadarBooleanState; // What was state on the init stage
  SwitchedState: AdaptRadarBooleanState; // Current state
}


/* rx-select */

export interface AdaptRadarComponentRxSelectData extends AdaptRadarComponentRxCommonData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Form element label
  NumberOfSelectedItems: number; // Number of currently selected items
  DefaultNumberOfSelectedItems: number; // Number of items that was selected on init stage
  Target?: 'selectAll' | 'loadMore'; // Target that was interacted with
  Required?: boolean; // Is form element required
  Disabled?: boolean; // Is form element disabled
  ReadOnly?: boolean; // Is form element readonly
  SelectType?: 'single' | 'multi'; // Type of the select
  // SelectLimit?: number; // We have no such configuration inside select. Limit is controlled on customer side.
  // DynamicEntry: boolean; // Is the value was entered by user or by system
  FieldSize?: FormControlSize; // Size of the form element
}


/* rx-datetime */

enum RxDatetimePickerMode {
  DateTime = 0,
  Date = 1,
  Time = 2,
  YearMonth = 3,
  Year = 4,
  Month = 5
}

export interface AdaptRadarComponentRxDateTimeData extends AdaptRadarComponentRxGenericData {
  Type: RxDatetimePickerMode; // Datetime mode
  Range: boolean; // Is datetime component work in range mode
  Inline: boolean; // Is inline mode enabled
  InputMethod?: AdaptInteractionSource; // From what source submission was provided
  // DynamicEntry: boolean; // Is the value was entered by user or by system
}


/* rx-timeslot */

export interface AdaptRadarComponentRxTimeSlotData extends AdaptRadarComponentRxGenericData {
  Slots: number; // Number of component slots
}


/* rx-list-selector */

export interface AdaptRadarComponentRxListSelectorData extends AdaptRadarComponentRxGenericData {
  DefaultNumberOfSelectedItems: number; // Number of selected option on init stage
  TotalSelected: number; // Number of selected options
  TotalOptions: number; // Number of list selector options
}


/* rx-list-builder */

export interface AdaptRadarComponentRxListBuilderData extends AdaptRadarComponentRxGenericData {
  Action: 'add' | 'delete'; // Action that was triggered
  ListCount: number; // Number of first level list array
  MaxDepth: number; // Maximum dept of the list
  // TODO: Need to specify what this property mean one more time
  // TotalParents: number; // first level items count. Make sense only for 'delete' action
  TotalChildren?: number; // items on first level for current item. Make sense only for 'delete' action
  TotalGrandchildren?: number; // items on second level for current item. Make sense only for 'delete' action
}


/* rx-feedback */

export interface AdaptRadarComponentRxFeedbackData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Label: string; // Form element label
  Required: boolean; // Is form element required
  ParentComponent: AdaptRadarCustomEventName; // The name of the parent component
  ErrorsTriggered: string[]; // Validator names that was triggered
}


/* navigation */

type NavigationActionType = 'submenu' | 'switcher';
type NavigationActionItemType = 'link' | 'route' | 'action';

export interface AdaptRadarComponentNavigationData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  HamburgerMode: boolean; // Is navigation in hamburger mode
  NavigationColor: string; // Theme color
  Logo: string; // Logo class
  MenuCount: number; // Number of menu items
  ActionCount: number; // Number of action items
  Title: string; // Name of the product
  ActionsLoaded: string[]; // List of loaded actions names
}

export interface AdaptRadarComponentNavigationHamburgerData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  HamburgerMode: boolean; // Is navigation in hamburger mode
  ContainerSize: number; // Navigation element width in pixels
  MenuCount: number; // Number of menu items
  ActionCount: number; // Number of action items
  ProfilePresent: boolean; // Is profile present
}

export interface AdaptRadarComponentNavigationMenuData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Disabled: boolean; // Is interacted menu item disabled
  Name: string; // Interacted menu item name
  Type: NavigationActionItemType; // Interacted menu item type
  MaxDepth: number; // Maximum depth of the menu
  SiblingsOptionsCount: number; // Number of not hidden siblings for the interacted menu item
  HiddenSiblingsOptionsCount: number; // Number of hidden siblings for the interacted menu item
}

export interface AdaptRadarComponentNavigationActionData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  ActionType: NavigationActionType; // Interacted action item action type
  // TODO: do we need to pass name here? It can be private info
  Name: string; // Interacted action name
  Type: NavigationActionItemType; // Interacted action item type
}

export interface AdaptRadarComponentNavigationAlertData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Type: string; // Alert type
  Title: string; // Alert title
}


/* menu */

export interface AdaptRadarComponentMenuData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  ParentComponent: AdaptRadarCustomEventName; // Parent component name
  Disabled: boolean; // Is menu item disabled
  Name: string; // Menu item name
  Type: 'link' | 'route' | 'action' | 'menu'; // Menu item type
  SiblingsOptionsCount: number; // Number of not hidden siblings for the interacted menu item
  HiddenSiblingsOptionsCount: number; // Number of hidden siblings for the interacted menu item
}


/* profile */

export interface AdaptRadarComponentProfileData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  Type: 'long' | 'short'; // Type of the profile
}


/* tabs */

type TabsType = 'pills' | 'tabs' | 'inverted' | 'bordered' | 'stacked' | 'justified' | 'responsive';

export interface AdaptRadarComponentTabsData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  ComponentEventType: AdaptRadarComponentEventType; // Used to specify component related event (eg. Tab added or Tab deleted)
  TabType: TabsType; // Used to specify tabs type ('pills' | 'tabs' | 'inverted' | 'bordered' | 'stacked' | 'justified' | 'responsive')
  TabLabel: string; // Tab title
  TabIcon: boolean; // Is Tab has icon
  TabBadge: boolean; // Is Tab has badge
  TabMenu: boolean; // Is Tab has a kebab menu
  Disabled: boolean; // Is Tab disabled
  TabPosition: number; // Used to specify tab position. Starts from 0
  TabCount: number; // Count of tabs
}

export interface AdaptRadarComponentTabsKebabMenuData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  TabLabel: string; // Tab title
}

/* accordion */

export interface AdaptRadarComponentAccordionData {
  EventType: AdaptRadarCustomEventType; // Used to specify type of event that was fired
  AccordionType: 'single' | 'multi'; // Show if accordion allowing to have more than one opened tab
  HeaderLabel: string; // Accordion tab title text
  HeaderIcon: boolean; // Show if accordion tab icon present
  Disabled: boolean; // Is accordion tab disabled
  SectionOpen: boolean; // Is accordion tab opened
  OpenSections: number; // Number of opened accordion tabs
}
