import {AfterViewInit, Directive, ElementRef, Input, OnDestroy} from '@angular/core';
import {AdaptRadarService} from './adapt-radar.service';
import {AdaptRadarCustomEventName} from './adapt-radar.model';


@Directive()
export abstract class AdaptRadarGenericDirective implements AfterViewInit, OnDestroy {

  private _elementIntersectObserver: IntersectionObserver;

  @Input() adaptRadarGroupName: string = null;
  @Input() adaptRadarDisableEventSending: boolean = this._checkIfDisabledFromSending();

  constructor(protected readonly _adaptRadarService: AdaptRadarService,
              protected readonly _elem: ElementRef<HTMLElement>,
              protected readonly _entityName: AdaptRadarCustomEventName) {
  }

  ngAfterViewInit(): void {
    this._elementIntersectObserver = this._adaptRadarService.subscribeOnElementIntersect(this.adaptRadarDisableEventSending, this._elem, this._entityName);
  }

  ngOnDestroy(): void {
    this._unsubscribeFromIntersectionObserver();
  }

  private _unsubscribeFromIntersectionObserver(): void {
    if (this._elementIntersectObserver) {
      this._elementIntersectObserver.disconnect();
      this._elementIntersectObserver = null;
    }
  }

  private _checkIfDisabledFromSending(): boolean {
    return !!this._adaptRadarService.config?.disableEventSending?.get(this._entityName);
  }
}
