export interface AdaptRadarGainsightIdentifyUserData {
  // Properties from Gainsight
  id: string;
  accountId?: string;
  city?: string;
  continent?: string;
  countryCode?: string;
  countryName?: string;
  email?: string;
  firstName?: string;
  firstVisitDate?: number;
  gender?: string;
  guidedTours?: boolean;
  lastName?: string;
  latitude?: string;
  leadDate?: number;
  longitude?: string;
  numberOfVisits?: number;
  onboardingBot?: boolean;
  phone?: string;
  postalCode?: string;
  productUpdates?: boolean;
  regionName?: string;
  role?: string;
  score?: number;
  signUpDate?: number;
  stateCode?: string;
  stateName?: string;
  street?: string;
  subscriptionId?: string;
  surveys?: boolean;
  timeZone?: string;
  jobTitle?: string;
  trackUsage?: boolean;

  // properties added by ADAPT
  customer?: string;

  // CSN is Client Serial Number - found this within a my-bmc article.
  CSN?: string;

  // SFDC ID and CSN are unique identifiers for an Account record within Salesforce
  // SFDC is Salesforce.com – SFDC id is the ID Salesforce gives to a new record
  SFDC?: string;

  // https://support.gainsight.com/PX/Install_PX/01_Install_Gainsight_PX_on_your_web_app#Code_the_Identify_Call_In_Your_Application_(Developer_Assistance_Needed)
  globalId?: string;
}

